@font-face {
  font-family: 'GoodTimes-Regular';
  src: local('GoodTimes-Regular'),
    url('../assets/fonts/Good-Times-Font/good times rg.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'GoodTimes-Bold';
  src: local('GoodTimes-Bold'),
    url('../assets/fonts/Good-Times-Font/Good Times W00 Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

h3,
p {
  margin: 0;
}
body {
  color: var(--main-text-color);
  background-color: var(--background-color);
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;

  &.modal-open {
    overflow: hidden;
  }
  &.mobile-menu-open {
    overflow: hidden;
  }

  @media screen and (min-width: $tablet) {
  }
  @media screen and (min-width: $desktop) {
  }
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.link {
  display: inline-block;
  text-decoration: none;
  padding: 15px 0;
  color: inherit;
}
.container {
  padding: 0;
  margin: 0 auto;
  @media screen and (max-width: $telephone) {
    width: 100%;
  }
  width: $telephone;
  @media screen and (min-width: $tablet) {
    width: $tablet;
  }
  @media screen and (min-width: $desktop) {
    width: $desktop;
  }
}
.section {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: var(--background-color);
  @media screen and (min-width: $tablet) {
  }
  @media screen and (min-width: $desktop) {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}
.section-title {
  margin-top: 0;
  margin-bottom: 30px;
  color: var(--secondary-text-color);
  font-size: 28px;
  line-height: calc(33 / 28);
  text-align: center;
  letter-spacing: 0.03em;
  @media screen and (min-width: $tablet) {
  }
  @media screen and (min-width: $desktop) {
    font-size: 36px;
    line-height: calc(42 / 36);
    margin-bottom: 50px;
  }
}
